@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
*,::before,::after{box-sizing:border-box}body{height:100vh;background:#202020;color:whitesmoke;display:flex;justify-content:center;font-family:'Nunito', sans-serif}h1{text-align:center;font-size:27px}form{display:grid;place-items:center;margin-top:60px}form label+label{margin-top:2rem}form label select,form label input{padding:3px 5px;margin:0 0 0 10px;border:1px solid whitesmoke;border-radius:7px;font-family:'Nunito', sans-serif;background:#202020;color:whitesmoke}form label select{cursor:pointer}.dynamic-color{display:grid;place-items:center;margin-top:60px}.dynamic-color .circle h3{white-space:nowrap;word-wrap:hidden;overflow:hidden;color:#a09f9f}

